@mixin transparent-background {
	background: rgba(0, 0, 0, 0.5); }

nav.main-nav {
	width: 100%;
	position: absolute;
	button.main-nav-button {
		cursor: pointer;
		position: relative;
		z-index: 99;
		margin: auto;
		border: none;
		outline: none;
		padding: .5rem;
		display: block;
		border-bottom-right-radius: 0.5rem;
		border-bottom-left-radius: 0.5rem;
		transform: translateY(-2rem);
		transition: ease-in-out .5s;
		&:hover {
			transform: translateY(0); }
		@include transparent-background;
		@media screen and (min-width: $break-medium) {
			display: none; }
		p {
			font-size: 1.25rem;
			margin: 0;
			color: $white; }
		svg {
			fill: $white;
			margin-top: 0.5rem;
			max-width: 50px; } }

	ul {
		z-index: 9;
		transform: translateY(-100%);
		list-style: none;
		margin: 0;
		padding: 0;
		transition: ease-in-out 0.5s;
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		padding-top: 5rem;
		@include transparent-background;
		li {
			text-align: center;
			a {
				text-decoration: none;
				color: $white;
				width: 100%;
				display: block;
				font-size: 2rem;
				padding: 0.5rem 0;
				&:hover {
					background: $blue; } } }
		@media screen and (min-width: $break-medium) {
			transform: translateY(0%);
			position: relative;
			padding: 0;
			li {
				a {
					width: 20%;
					float: left;
					font-size: 1.5rem;
					@include transparent-background;
					@media screen and (min-width: $break-large) {
						font-size: 2rem; } } } } } }
nav.main-nav {
	ul.show {
		transform: translateY(0%); }
	.main-nav-button.out {
		transform: translateY(0);
		svg {
			transform: rotate(180deg); } } }
